* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  tap-highlight-color: transparent;
  -webkit-tap-highlight-color: transparent;
  scroll-behavior: smooth;
}

html {
  font-size: 10px;
}

body {
  background-color: #f5f5f5;
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 1.6rem;
  line-height: 2.4rem;
  color: #0a302c;
}

.body--white {
  background-color: #ffffff;
}
